<template>
  <div class="domains-logo">
    <a href="./" class="domains-logo__link" v-bind:class="getColorSchemeLogo">
      <span class="domains-logo__text" v-bind:class="getColorSchemeText">домены</span>
    </a>
  </div>
</template>

<script>

export default {
  name: 'DomainsLogo',
  props: {
    colorScheme: {
      type: String,
      required: false
    }
  },
  computed: {
    getColorSchemeLogo: function() {
      return {
        'domains-logo__link_white': this.$props.colorScheme === 'white'
      }
    },
    getColorSchemeText: function() {
      return {
        'domains-logo__text_white': this.$props.colorScheme === 'white'
      }
    }
  }
}
</script>

<style lang="scss">

.domains-logo{
  text-align: left;
}

.domains-logo__link{
  background: url('../assets/logo.svg') no-repeat;
  padding: 23px 10px 23px 76px;
  display: inline-block;
  background-position-y: 27px;
  text-decoration: none;
}

.domains-logo__link_white{
  background: url('../assets/logo-white.svg') no-repeat;
  background-position-y: 27px;
}


.domains-logo__text{
  color: #333333;
  font-weight: 700;
}

.domains-logo__text_white{
  color: #fff;
}

</style>
