<template>
  <div class='navigation-dots'>
    <span 
      class='navigation-dots__dot' 
      v-for='(point, index) in breakpoints'
      v-bind:key = 'index'
      v-bind:class=' active == index ? "navigation-dots__dot_active" : ""'
      @click='handleClick(index)'
    ></span>
  </div>
</template>

<script>

export default {
  name: 'NavigationDots',
  data(){
    return {
      active: 0,
      scroll: 0,
      breakpoints: [],
      interval: {}
    }
  },
  mounted(){
    window.addEventListener('scroll', this.getScroll);
    this.breakpoints = document.getElementsByClassName('navigation-dots__breakpoint')
  },
  beforeDestroy(){
    window.removeEventListener('scroll', this.getScroll)
  },
  watch: {
    scroll() {
      if (!this.breakpoints){
        return
      }

      for (var index = 0; index < this.breakpoints.length; index++ ){

        if(!this.breakpoints[index + 1]){
          this.active = index;
          return
        }

        let point = this.breakpoints[index];
        let nextPoint = this.breakpoints[index + 1];
        var offset = 300;

        if( this.scroll + offset >= this.getTop(point) 
          && this.scroll + offset <= this.getTop(nextPoint)){
          this.active = index
          break
        }
      }
    }
  },
  methods: {
    getScroll(){
      let scroll = window.pageYOffset || document.documentElement.scrollTop;
      this.scroll = scroll;
    },
    getTop(elem) {
      let box = elem.getBoundingClientRect();
      return box.top + window.pageYOffset
    },
    handleClick(index){

      let scrollTo = this.getTop(this.breakpoints[index]);

      this.animate(scrollTo)
    },
    animate( scrollTo ){

      let difference = scrollTo - this.scroll;
      let direction = 1;

      if( difference < 0){
        direction = - direction;
      }

      if ( this.interval.completed == false  ) {
        clearInterval(this.interval.ref)
      }

      this.interval = {
        count: 0,
        direction,
        completed: false,
        scrollTo
      }


      this.interval.ref = setInterval(this.tickAnimate, 10)
    },

    tickAnimate(){
      let timer = this.interval;

      if( timer.count > 50 ){
        this.endTick(timer)
        return
      }
      timer.count++;

      if( ( timer.scrollTo - this.scroll ) * timer.direction < 5){
        this.endTick(timer)
        return
      }

      let tickScroll = (timer.scrollTo - this.scroll) / 7;

      if ( tickScroll < 5 && tickScroll > - 5 ) {
        tickScroll = 4 * timer.direction
      }

      if ( 
          this.scroll > this.getDocumentHeight() - document.documentElement.clientHeight - 10
          && timer.direction == 1
        ) {
        this.endTick(timer)
        return
      } 
      if ( this.scroll <= 2 && timer.direction == -1){
        this.endTick(timer)
        return
      }

      window.scrollBy(0, tickScroll);
    },
    endTick(timer) {
      window.scrollTo(0, timer.scrollTo);
      clearInterval(timer.ref)
      this.interval.completed = true
    },
    getDocumentHeight(){
      return Math.max(
        document.body.scrollHeight, document.documentElement.scrollHeight,
        document.body.offsetHeight, document.documentElement.offsetHeight,
        document.body.clientHeight, document.documentElement.clientHeight
      );
    }
  }
}
</script>

<style lang="scss">

@import '../grid.scss';

.navigation-dots{
  position: fixed;
  right: 12px;
  display: none;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  z-index: 1000;

  @include media-breakpoint(MEDIUM){ 
    display: flex;
  }

  @include media-breakpoint(LONG){ 
    right: 28px;
  }
}

.navigation-dots__dot{
  margin-top: 9px;
  margin-bottom: 9px;
  background: #fff;
  display: block;
  border-radius: 50%;
  border: solid 1px #000;
  height: 12px;
  width: 12px;
  position: relative;
  z-index: 1000;
}

.navigation-dots__dot_active{
  border-color: #fff;
  background: #000;
}

.navigation-dots__breakpoint{
}
</style>