<template>
  <li class="info-item">
    <div class="info-item__cover" @click='$emit("drop-down")'>
      <slot name='icon'></slot>
      <h5 class="info-item__title">
        <slot name='title'></slot>
      </h5>
    </div>
    <div class="info-item__wrapper" v-bind:class='{"info-item__wrapper_active": showDescription}'>
      <transition 
        @enter='enterDescription'
        @before-leave='beforeLeaveDescription'
        @leave='leaveDescription'
        name="info-item_show"
        @before-enter='beforeEnterDescription'
        @after-enter="clearStyle"
        appear
      >
        <div class="info-item__wrapper-inner" v-if='showDescription'>
          <div class="info-item__description" ref='description'>
            <slot name='description'></slot>
          </div>
        </div>
      </transition>
    </div>
    <transition name="info-item_show"  @leave='leaveSpace'  @enter='enterSpace' appear>
      <div class="info-item__space" v-if='showDescription' v-bind:style='{height: spaceHeight}' ref='space'></div>
    </transition>
  </li>
</template>

<script>

export default {
  name: 'InfoItem',
  props: {
    showDescription: Boolean
  },
  mounted () {
    window.addEventListener( 'resize', this.getWindowWidth)
  },
  beforeDestroy () {
    window.removeEventListener( 'resize', this.getWindowWidth )
  },
  data() {
    return {
      windowWidth: 0,
      spaceHeight: '0px'
    }
  },
  watch:{
    windowWidth(){

      if(!this.$refs.space){
        return
      }
      
      this.spaceHeight = this.$refs.description.clientHeight + 2 + 'px';

    }
  },
  methods: {

    getWindowWidth(){
      this.windowWidth = window.innerWidth;
    },

    clearStyle(el) {
      el.classList.remove('info-item__wrapper-inner_height_zero');
      el.style.cssText = '';
    },

    enterSpace(el) {
      el.style.height =  this.$refs.description.clientHeight + 2 + 'px';
      el.style.marginTop = '51px';
    },

    leaveSpace(el) {
      el.style.height = '0px';
      el.style.marginTop = '0px';
    },

    beforeEnterDescription (el) {
      el.classList.add('info-item__wrapper-inner_height_zero')
    },

    enterDescription(el) {
      el.style.height = this.$refs.description.clientHeight + 2 + 'px';
      this.spaceHeight = this.$refs.description.clientHeight + 2 + 'px';
    },

    beforeLeaveDescription (el) {
      el.style.height = this.$refs.description.clientHeight + 2 + 'px';
    },

    leaveDescription(el) {
      el.style.height = this.$refs.description.clientHeight + 2 + 'px';
      el.style.height = '0px';
      this.spaceHeight = '0px';
    }
  }
}


</script>

<style lang="scss">

@import '../grid';

.info-item{
  position: relative;
  list-style: none;
  margin-top: 50px;
  width: 100%;

  @include media-breakpoint(SMALL){
    @include columns(6);
  }

  @include media-breakpoint(LONG){
    @include columns(3);
      margin-top: 32px;
  }
}

.info-item__cover>img{
  width: 64px;
  height: 64px;
}

.info-item__space{
  height: 0px;
  margin-top: 0px;
}

.info-item__title{
  font-size: 21px;
  font-weight: 400;
  margin-top: 13px;
  letter-spacing: -0.12ex;
  letter-spacing: -0.05ex;
  margin-bottom: 0px;
  padding: 0px 5px;
}

.info-item__wrapper-inner{
  overflow-y: hidden;
  opacity: 1;
  border: solid 1px rgba(95, 96, 98, 0.5);
  background: rgb(251, 251, 251);
  border-radius: 10px;
  position: relative;
}

.info-item__wrapper-inner_height_zero{
  height: 0px;
}

.info-item__wrapper{
  z-index: 1;
  top: 157px;
  position: absolute;
  
  @include media-breakpoint(SMALL){
    width: 100% * 2 + get-gutters-width(1) * 100 / get-columns-width-with-gutters(6);
  }

  @include media-breakpoint(LONG){
    width: 100% * 4 + get-gutters-width(3) * 100 / get-columns-width-with-gutters(3);
  }

}

.info-item__wrapper::before{
  transition: opacity 100ms;
  opacity: 0;
  display: block;
  content: '';
  position: absolute;
  height: 20px;
  width: 20px;
  z-index: 1;
  border-top: solid 1px rgba(95, 96, 98, 0.5);
  border-left: solid 1px rgba(95, 96, 98, 0.5);
  background: rgb(251, 251, 251);
  top: -10px;
  transform: rotateZ(45deg);
  left: calc(50% - 14px);
}


.info-item__wrapper_active::before{
  opacity: 1;
}

.info-item__description>p{
  margin: 0;
  text-align: left;
}

.info-item__description{
  padding: 22px;

  @include media-breakpoint(MEDIUM){
    padding: 22px 40px 25px;
  }
}


.info-item_show-enter-active, .info-item_show-leave-active{
  transition: 500ms;
}

.info-item_show-leave-to, .info-item_show-enter{
  opacity: 0;
}




@include media-breakpoint(SMALL){

  $element-width: get-columns-width-with-gutters(6);
  $half-element-offset: $element-width / 2;

  .info-item_position_1>.info-item__wrapper::before,
  .info-item_position_3>.info-item__wrapper::before{
    left: calc( #{$half-element-offset} - 14px );
  }


  .info-item_position_2>.info-item__wrapper, 
  .info-item_position_4>.info-item__wrapper{
    margin-left: -100% - get-gutters-width(1) * 100 / $element-width
  }

  .info-item_position_2>.info-item__wrapper::before, 
  .info-item_position_4>.info-item__wrapper::before{
    $offset: $half-element-offset + ( get-columns-width(6) + get-gutters-width(6) );
    left: calc( #{$offset} - 14px );
  }
}

@include media-breakpoint(LONG){

  $element-width: get-columns-width-with-gutters(3);
  $half-element-offset: $element-width / 2;

  .info-item_position_1>.info-item__wrapper::before{
    left: calc( #{$half-element-offset} - 14px );
  }


  .info-item_position_2>.info-item__wrapper{
    margin-left: -100% - get-gutters-width(1) * 100 / $element-width
  }

  .info-item_position_2>.info-item__wrapper::before{
    $offset: $half-element-offset + ( get-columns-width(3) + get-gutters-width(3) );
    left: calc( #{$offset} - 14px );
  }


  .info-item_position_3>.info-item__wrapper{
    margin-left: -100% * 2 - get-gutters-width(2) * 100 / $element-width
  }

  .info-item_position_3>.info-item__wrapper::before{
    $offset: $half-element-offset + ( get-columns-width(3) + get-gutters-width(3) ) * 2;
    left: calc( #{$offset} - 14px );
  }


  .info-item_position_4>.info-item__wrapper{
    margin-left: -100% * 3 - get-gutters-width(3) * 100 / $element-width
  }

  .info-item_position_4>.info-item__wrapper::before{
    $offset: $half-element-offset + ( get-columns-width(3) + get-gutters-width(3) ) * 3;
    left: calc( #{$offset} - 14px );
  }

}

</style>