<template>
  <form @submit.prevent='hadnleSubmit' class="domain-check-form">
    <input type="text" class="domain-check-form__input" v-model='domain'>
    <input type="submit" class="domain-check-form__submit" value='Проверить'>
    <p class="domain-check-form__response">
      {{ checked ?  `Домен ${checked} — свободен.` : '' }}
      <a class="domain-check-form__link" v-if='checked' href="./">Зарегистрировать за 39&#8381;</a>
    </p>
  </form>
</template>

<script>

export default {
  name: 'DomainCheckForm',
  data(){
    return {
      domain: 'example.ru',
      checked: ''
    }
  },
  methods: {
    hadnleSubmit() {
      this.checked = this.domain;
    }
  }
}
</script>

<style lang="scss">

@import '../grid';

.domain-check-form{
  position: relative;
  background: #fff;
  border-radius: 7px;
  background-color: rgba(51, 51, 51, 0.9);
  margin-top: 66.4px;
  min-height: 124px;

  @include columns(12);
  
  @include media-breakpoint(MEDIUM){
    @include columns(10);
    @include offset-left(1)
  }

  @include media-breakpoint(LONG){
    @include columns(8);
    @include offset-left(2)
  }
}

.domain-check-form__input{
  width: 100%;
  height: 80px;
  border: none;
  border-radius: 7px;
  color: rgb(51, 51, 51);
  font-size: 20px;
  padding: 4px 130px 10px 36px;
  letter-spacing: -0.017ex;

  @include media-breakpoint(SMALL){
    font-size: 24px;
    padding-right: 183px;
  }
}

.domain-check-form__submit{
  background-color: #5f6062;
  color: #fff;
  border: none;
  position: absolute;
  right: 15px;
  top: 14px;
  border-radius: 7px;
  height: 52px;
  padding: 10px 10px 10px;
  font-size: 18px;
  cursor: pointer;

  @include media-breakpoint(SMALL){
    width: 158px;
    font-size: 21px;
    padding: 10px 10px 14px;
  }
}

.domain-check-form__response{
  color: #fff;
  margin: 11px 26px 14px 20px;
}

.domain-check-form__link{
  color: rgb(119, 181, 60);
}

</style>